    height: 100%;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: rgba(10, 10, 10, .45)
}

.start-page .title-block {
    position: relative;
    text-align: center
}

.start-page .title-block h2 {
    color: #fff;
    font-size: 70px;
    margin: 0;
    text-align: center
}

.start-page .title-block .sp-subtitle {
    color: #fff;
    font-size: 21px;
    font-weight: 300;
    margin: 5px 0;
    text-align: center
}

.start-page .vcentered {
    display: table-cell;
    vertical-align: middle;
    width: 100%
}

.lm-info-block.gray-bg {
    background-color: #fafafa
}

.lm-info-block .lm-info-block-value {
    font-size: 30px;
    line-height: 30px;
    font-weight: 400;
    display: block;
    margin: 10px 0;
    color: rgb(37, 92, 37);
}

.lmpixels-scroll-to-top {
    position: fixed;
    bottom: 10px;
    right: 15px;
    z-index: 100;
    display: inline-block;
    padding: 0;
    background-color: #fafafa;
    height: 50px;
    width: 50px;
    text-align: center;
    line-height: 50px;
    font-size: 24px;
    cursor: pointer;
    border: 1px solid #e5e5e5;
    color: #333;
    opacity: 1;
    visibility: visible;
    -webkit-transition: all .2s ease-in-out;
    -moz-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    -ms-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    -webkit-box-shadow: 0 0 12px 0 rgba(0, 0, 0, .12);
    -moz-box-shadow: 0 0 12px 0 rgba(0, 0, 0, .12);
    box-shadow: 0 0 12px 0 rgba(0, 0, 0, .12)
}

.lmpixels-scroll-to-top:hover {
    color: rgb(37, 92, 37);
}

.lmpixels-scroll-to-top.hidden {
    opacity: 0;
    visibility: hidden
}

.info-list {
    display: inline-block;
    margin: 0;
    padding: 0;
    list-style: none
}

.info-list li {
    margin: 6px 0;
    text-align: left
}

.info-list li .title {
    display: inline-block;
    font-weight: 600;
    margin-right: 15px
}

.info-list li .title:after {
    content: '';
    display: block;
    width: 100%;
    height: 2px;
    background-color: rgb(37, 92, 37);
}

@media only screen and (max-width:991px) {
    .pt-page.start-page {
        padding-left: 0
    }
}

@media only screen and (max-width:768px) {
    .start-page .title-block h2 {
        font-size: 42px
    }

    .start-page .title-block .sp-subtitle {
        font-size: 18px
    }
}

@media only screen and (max-width:480px) {
    .lmpixels-arrows-nav>div {
        height: 34px;
        width: 34px;
        line-height: 34px
    }
}

.section-inner.start-page-full-width .hp-text-block .sp-subtitle {
    color: #888;
    font-weight: 300
}

.header .copyrights {
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 5px 10px 15px;
    line-height: 14px;
    font-size: 12px;
    text-align: center;
    color: #aeaeae
}

body {
    overflow: auto
}

.one-page-style .site-main,
.one-page-style .pt-wrapper,
.one-page-style .subpages {
    min-height: auto;
    max-height: none;
    height: auto
}

.one-page-style .pt-page {
    position: relative;
    height: auto;
    overflow: hidden;
    min-height: auto;
    padding-bottom: 10px;
    opacity: 1;
    visibility: visible
}

.one-page-style .pt-page:last-child {
    padding-bottom: 0
}

.one-page-style .pt-page .section-inner:not(.start-page-full-width) {
    padding-top: 40px
}

.one-page-style .pt-page:last-child .section-inner:not(.start-page-full-width) {
    padding-bottom: 70px
}

.one-page-style .lmpixels-arrows-nav {
    position: fixed
}

.blog-masonry {
    margin-bottom: 0
}

.size-blog-masonry-image-two-c {
    width: 100%;
    height: 250px;
    object-fit: cover;
}